@keyframes swal2-animate-i-mark {
  0% {
    transform: rotateZ(45deg);
    opacity: 0;
  }
  25% {
    transform: rotateZ(-25deg);
    opacity: 0.4;
  }
  50% {
    transform: rotateZ(15deg);
    opacity: 0.8;
  }
  75% {
    transform: rotateZ(-5deg);
    opacity: 1;
  }
  100% {
    transform: rotateX(0);
    opacity: 1;
  }
}
body.swal2-shown:not(.swal2-no-backdrop):not(.swal2-toast-shown) {
  overflow: hidden;
}
body.swal2-height-auto {
  height: auto !important;
}
body.swal2-no-backdrop .swal2-container {
  background-color: transparent !important;
  pointer-events: none;
}
body.swal2-no-backdrop .swal2-container .swal2-popup {
  pointer-events: all;
}
body.swal2-no-backdrop .swal2-container .swal2-modal {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}
@media print {
  body.swal2-shown:not(.swal2-no-backdrop):not(.swal2-toast-shown) {
    overflow-y: scroll !important;
  }
  body.swal2-shown:not(.swal2-no-backdrop):not(.swal2-toast-shown) > [aria-hidden=true] {
    display: none;
  }
  body.swal2-shown:not(.swal2-no-backdrop):not(.swal2-toast-shown) .swal2-container {
    position: static !important;
  }
}
body.swal2-toast-shown .swal2-container {
  box-sizing: border-box;
  width: 360px;
  max-width: 100%;
  background-color: transparent;
  pointer-events: none;
}
body.swal2-toast-shown .swal2-container.swal2-top {
  top: 0;
  right: auto;
  bottom: auto;
  left: 50%;
  transform: translateX(-50%);
}
body.swal2-toast-shown .swal2-container.swal2-top-end, body.swal2-toast-shown .swal2-container.swal2-top-right {
  top: 0;
  right: 0;
  bottom: auto;
  left: auto;
}
body.swal2-toast-shown .swal2-container.swal2-top-start, body.swal2-toast-shown .swal2-container.swal2-top-left {
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
}
body.swal2-toast-shown .swal2-container.swal2-center-start, body.swal2-toast-shown .swal2-container.swal2-center-left {
  top: 50%;
  right: auto;
  bottom: auto;
  left: 0;
  transform: translateY(-50%);
}
body.swal2-toast-shown .swal2-container.swal2-center {
  top: 50%;
  right: auto;
  bottom: auto;
  left: 50%;
  transform: translate(-50%, -50%);
}
body.swal2-toast-shown .swal2-container.swal2-center-end, body.swal2-toast-shown .swal2-container.swal2-center-right {
  top: 50%;
  right: 0;
  bottom: auto;
  left: auto;
  transform: translateY(-50%);
}
body.swal2-toast-shown .swal2-container.swal2-bottom-start, body.swal2-toast-shown .swal2-container.swal2-bottom-left {
  top: auto;
  right: auto;
  bottom: 0;
  left: 0;
}
body.swal2-toast-shown .swal2-container.swal2-bottom {
  top: auto;
  right: auto;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
body.swal2-toast-shown .swal2-container.swal2-bottom-end, body.swal2-toast-shown .swal2-container.swal2-bottom-right {
  top: auto;
  right: 0;
  bottom: 0;
  left: auto;
}

.dashboard-tab {
  display: flex;
  justify-content: space-between;
}
.dashboard-tab__asside {
  flex: 0 0 30rem;
  padding-right: 3rem;
  padding-bottom: 2rem;
  border-right: 1px solid #F5F5F5;
}
.dashboard-tab__general {
  flex: 0 0 calc(100% - 40rem);
  display: none;
}
.dashboard-tab__asside-tabs {
  margin-top: 1rem;
}
.dashboard-tab__get-help {
  margin-top: 2rem;
}
.dashboard-tab__tab-name {
  font-size: 1.4rem;
  font-weight: 600;
}
.dashboard-tab__items {
  margin-top: 1.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
  align-items: flex-start;
}
.dashboard-tab__general--active {
  display: block;
}

@media screen and (max-width: 1199px) {
  .dashboard-tab__general {
    margin-left: 3rem;
    flex-basis: auto;
  }
  .dashboard-tab__items {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    justify-items: center;
  }
  .dashboard-tab__next-item, .dashboard-tab__since-item {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .dashboard-tab {
    display: block;
  }
  .dashboard-tab__asside {
    border-right: none;
    padding-right: 0;
  }
  .dashboard-tab__general {
    margin-left: 0;
  }
  .dashboard-tab__items {
    margin: 2rem auto 0 auto;
    width: 30rem;
  }
}
.service-history {
  display: grid;
  grid-template-columns: 30rem 47rem;
  grid-gap: 10rem;
}
.service-history__asside {
  padding-right: 3rem;
}
.service-history__title {
  font-size: 1.4rem;
}
.service-history__items-wr {
  margin-top: 1.5rem;
}
.service-history__item:not(:first-child) {
  margin-top: 2rem;
}

@media screen and (max-width: 1199px) {
  .service-history {
    grid-gap: 3rem;
  }
}
@media screen and (max-width: 991px) {
  .service-history {
    grid-template-columns: 1fr;
  }
  .service-history__asside {
    padding-right: 0;
  }
}
.repair-costs {
  text-align: center;
  padding-top: 6.5rem;
}
.repair-costs__text {
  font-size: 2.4rem;
  line-height: 140%;
  font-weight: 600;
}

.dashboard-media__items-wr {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 2rem 3rem;
}
.dashboard-media__item {
  position: relative;
  aspect-ratio: 1/1;
}
.dashboard-media__check {
  position: absolute;
  top: 1rem;
  right: 1rem;
  opacity: 0;
  visibility: hidden;
}
.dashboard-media__radio {
  position: absolute;
  top: 1rem;
  right: 1rem;
  opacity: 0;
  visibility: hidden;
}
.dashboard-media__items-wr--checks .dashboard-media__check {
  opacity: 1;
  visibility: visible;
}
.dashboard-media__items-wr--radios .dashboard-media__radio {
  opacity: 1;
  visibility: visible;
}
.dashboard-media__heading-btns {
  display: none;
  grid-template-columns: repeat(2, 8.5rem);
  grid-gap: 1rem;
}
.dashboard-media__heading-btns--active {
  display: grid;
}

@media screen and (max-width: 991px) {
  .dashboard-media__items-wr {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 1.5rem 2rem;
  }
}
@media screen and (max-width: 575px) {
  .dashboard-media__items-wr {
    margin-top: 2rem;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
  }
}
.appraisal__form {
  margin-top: 5rem;
}
.appraisal__grid {
  display: grid;
  grid-template-columns: 30rem 70rem;
  grid-gap: 7rem;
  align-items: flex-start;
}
.appraisal__general-form {
  display: grid;
  align-items: flex-start;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem 1.5rem;
}
.appraisal__general-field-wr {
  grid-column: 1/3;
}
.appraisal__general-field-wr--mileage {
  grid-column: 1/2;
}
.appraisal__general-field-wr--engine {
  grid-column: 2/3;
}
.appraisal__submit {
  margin: 5rem auto 0 auto;
  width: 37rem;
  display: block;
}

@media screen and (max-width: 1399px) {
  .appraisal__grid {
    grid-template-columns: 30rem auto;
    grid-gap: 3rem;
  }
}
@media screen and (max-width: 991px) {
  .appraisal__grid {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 575px) {
  .appraisal__form {
    margin-top: 2rem;
  }
  .appraisal__checks-wr {
    overflow-x: auto;
  }
  .appraisal__checks {
    min-width: 51rem;
  }
  .appraisal__submit {
    margin-top: 2rem;
    width: 100%;
  }
}
.content-tab {
  display: none;
}
.content-tab--active {
  display: inherit;
}

.custom-radio__field-wr {
  position: relative;
}
.custom-radio__field {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  opacity: 0;
  outline: none;
  z-index: 2;
  cursor: pointer;
}
.custom-radio__customize {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 0.4rem;
  border: 1px solid #0077C6;
  transition: 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}
.custom-radio__customize svg {
  opacity: 0;
  transition: 0.3s ease-in-out;
}
.custom-radio__field:checked + .custom-radio__customize {
  background: #0077C6;
}
.custom-radio__field:checked + .custom-radio__customize svg {
  opacity: 1;
}
.custom-radio__label {
  cursor: pointer;
}

.custom-radio--media {
  width: 2.7rem;
  height: 2.7rem;
}
.custom-radio--media .custom-radio__customize {
  background: #ffffff;
  width: 2.7rem;
  height: 2.7rem;
  border-color: #ffffff;
  border-radius: 50%;
}
.custom-radio--media .custom-radio__field:checked + .custom-radio__customize {
  background: #ffffff;
}
.custom-radio--media .custom-radio__field:checked + .custom-radio__customize svg {
  opacity: 1;
}